import React, { Component } from 'react';
import { connect } from 'react-redux';
import GameStarted from './GameStarted/GameStarted';
import '../../styles/Game.css';
import * as actionTypes from '../../store/actions';


class Game extends Component {
    state = {
        playerName: "player"
    }

    onStartGame = () => {
        if(this.state.playerName === ""){
            alert("Név nélkül nem lehet elindítani, kérlek add meg!");
        }else if(this.props.nmb === 0){
            alert("Ha nincs kérdés hozzáadva anélkül nem tudod elindítani, kérlek adj hozzá legalább 1 kérdést!");
        }
        else{
            this.props.onStartGame();
        }        
    }

    playerNameHandler = (event) => {
        this.setState({playerName: event.target.value});
    }

    render () {
        let game = null;
        if(this.props.started){
            game = <GameStarted
            playerName={this.state.playerName}/>
        }else{
            game = (<div>
                        <p>Add meg a játékos nevedet!</p>
                        <input onChange={event => this.playerNameHandler(event)} value={this.state.playerName}/>
                        <p/>
                        <button  className="styledButton" onClick={this.onStartGame}>Játék indítása</button>
                    </div>)
        }

        return (
            <div className="gameBox">
                {game}
            </div>
        );
    }
}


const mapStateToProps = state =>{
    return{
        nmb: state.numberOfQuestions,
        started: state.hasGameStarted
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onStartGame: () => dispatch({type: actionTypes.START_GAME})
    };
    
};

export default connect(mapStateToProps,mapDispatchToProps)(Game);