import React, {Component} from 'react';
import { connect } from 'react-redux';
import Questions from './Questions/Questions';
import '../../styles/Questioneditor.css';
import * as actionTypes from '../../store/actions';


class Questioneditor extends Component{
    state = { 
        question: '',
        choiceOne: '',
        choiceTwo: '',
        choiceThree: '',
        choiceFour: '',
        answer: 1
    }

    
    handleInputChange = (event) => {
        const target = event.target;
        const value = target.value;
        const name = target.name;
    
        this.setState({
            [name]: value
        });
    }    
    
    handleSubmit = (event) => {
        if(this.state.question === "" || this.state.choiceOne === "" || this.state.choiceTwo === "" || this.state.choiceThree === "" || this.state.choiceFour === ""){
            alert("Ki kell tölteni az összes mezőt, ahhoz hogy kérdést hozzáadd.");
        }else{
        this.props.onAddQuestion(this.state.question,this.state.choiceOne,this.state.choiceTwo,this.state.choiceThree,this.state.choiceFour,this.state.answer);
        this.setState({ 
            question: '',
            choiceOne: '',
            choiceTwo: '',
            choiceThree: '',
            choiceFour: '',
            answer: 1
        })
        }
        event.preventDefault();
    }

    render(){
        return (
            <div className="formAdd">
                <div className="scroll">
                    <Questions questionArray={this.props.qst}/>
                </div>
                <form className="addQuestion" onSubmit={this.handleSubmit}>
                    Kérdés hozzáadása:
                    <hr />
                    <div>
                        <label>
                            Kérdés:
                            <input name="question" type="text" value={this.state.question} onChange={this.handleInputChange} />
                        </label>
                    </div>
                    <div>
                        <label>
                            Első válasz:
                            <input name="choiceOne" type="text" value={this.state.choiceOne} onChange={this.handleInputChange} />
                        </label>
                    </div>
                    <div>
                        <label>
                            Második válasz:
                            <input name="choiceTwo" type="text" value={this.state.choiceTwo} onChange={this.handleInputChange} />
                        </label>
                    </div>
                    <div>
                        <label>
                            Harmadik válasz:
                            <input name="choiceThree" type="text" value={this.state.choiceThree} onChange={this.handleInputChange} />
                        </label>
                    </div>
                    <div>
                        <label>
                            Negyedik válasz:
                            <input name="choiceFour" type="text" value={this.state.choiceFour} onChange={this.handleInputChange} />
                        </label>
                    </div>
                    <div>
                        <label>
                            Melyik a helyes válasz? 
                            <select name="answer" value={this.state.answer} onChange={this.handleInputChange}>
                                <option value="1">Első</option>
                                <option value="2">Második</option>
                                <option value="3">Harmadik</option>
                                <option value="4">Negyedik</option>
                            </select>
                        </label>
                    </div>
                    <hr />
                        <input type="submit" value="Mentés" />
                </form>
            </div>
        );
    }
}



const mapStateToProps = state =>{
    return{
        qst: state.Questions
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onAddQuestion: (question,cone,ctwo,cthree,cfour,ans) => dispatch({type: actionTypes.ADD_QUESTION,question,cone,ctwo,cthree,cfour,ans})
    };
    
};


export default connect(mapStateToProps,mapDispatchToProps)(Questioneditor);