import React from 'react';
import '../../styles/Game.css'

const GameFinished = (props) => {
    return (
        <div>
            <p>Elfogytak a kérdések, vége a kvíznek!</p>
            <p>Összesen {props.points} pontot gyűjtöttél.</p>
            <button className="styledButton" onClick={props.restart}>Új játék</button>
        </div>
    )
}

export default GameFinished;