import React, {Component} from 'react';
import { connect } from 'react-redux';
import GameFinished from '../../../components/GameFinished/GameFinished';
import '../../../styles/Game.css'
import * as actionTypes from '../../../store/actions';


class GameStarted extends Component{
    state = {
        currentQuestion: 0,
        questionAnswered: false,
        points: 0,
        currentAnswer: null,
        activeOne: false,
        activeTwo: false,
        activeThree: false,
        activeFour: false
    }

    onNextQuestionHandler = () => {
        this.setCorrectAnswerColor(false);
        this.setState({currentQuestion: this.state.currentQuestion + 1,
        questionAnswered:false,
        activeOne: false,
        activeTwo:false,
        activeThree:false,
        activeFour:false,
        currentAnswer:null});
    }

    onGiveAnswerHandler = () => {
        if(+this.state.currentAnswer === +this.props.questions[this.state.currentQuestion].answer){
            this.setState({questionAnswered: true,
            points: this.state.points + 1});
            alert("Helyes válasz gratulálok!");
            this.setCorrectAnswerColor(true);
        }else if(this.state.currentAnswer === null){
            alert("Válassz ki egy válasz lehetőséget!");
        }else{
            this.setState({questionAnswered: true});
            alert("Rossz válasz, a helyes válasz beszíneződik!")
            this.setCorrectAnswerColor(true);
        }
    }

    setCorrectAnswerColor (showColor){
        document.getElementById(this.props.questions[this.state.currentQuestion].answer).style.backgroundColor = showColor ? "#1ac74e" : "";
    }

    getAnswerHandler = (event) => {
        if(!this.state.questionAnswered){
            this.setState({currentAnswer: event.target.value});
            switch(event.target.value){
                case "1":
                    this.setState({activeOne: true,activeTwo:false,activeThree:false,activeFour:false});
                    break;
                case "2":
                    this.setState({activeOne: false,activeTwo:true,activeThree:false,activeFour:false});
                    break;
                case "3":
                    this.setState({activeOne: false,activeTwo:false,activeThree:true,activeFour:false});
                    break;
                case "4":
                    this.setState({activeOne: false,activeTwo:false,activeThree:false,activeFour:true});
                    break;
                default:
                    break;
            }
        }   
    }

    render(){
        let game;

        if(this.state.currentQuestion === this.props.qstNumber){
            game = <GameFinished points={this.state.points} restart={this.props.onRestartGame}/>
        }else{
            game = (
                <div className="activeGame">  
                    <p>Üdvözöllek {this.props.playerName}! </p>
                    <p>{this.state.currentQuestion + 1}. kérdés:</p>
                    <p>{this.props.questions[this.state.currentQuestion].question}</p>
                        1. válasz: <button value="1" id="1" className={this.state.activeOne ? "styledButton active" : "styledButton"} onClick={event => this.getAnswerHandler(event)}>{this.props.questions[this.state.currentQuestion].choiceOne}</button>
                        &emsp; 2. válasz: <button value="2" id="2" className={this.state.activeTwo ? "styledButton active" : "styledButton"} onClick={event => this.getAnswerHandler(event)}>{this.props.questions[this.state.currentQuestion].choiceTwo}</button>
                        <p/>
                        3. válasz: <button value="3" id="3" className={this.state.activeThree ? "styledButton active" : "styledButton"} onClick={event => this.getAnswerHandler(event)}>{this.props.questions[this.state.currentQuestion].choiceThree}</button>
                        &emsp; 4. válasz: <button value="4" id="4" className={this.state.activeFour ? "styledButton active" : "styledButton"} onClick={event => this.getAnswerHandler(event)}>{this.props.questions[this.state.currentQuestion].choiceFour}</button>
                        <p/>
                        {this.state.questionAnswered === false &&
                            <button className="styledButton" onClick={this.onGiveAnswerHandler}>Küldés</button>}
                        {this.state.questionAnswered === true &&
                            <button className="styledButton" onClick={this.onNextQuestionHandler}> {this.state.currentQuestion === (this.props.qstNumber-1) ? "Játék vége" : "Tovább"}</button>}
                    <hr />
                        <p>Aktuális pontszám: {this.state.points}</p>
                        <p>Hátralévő kérdések száma: {this.props.qstNumber-(this.state.currentQuestion + 1)}</p>
                    <hr />
                </div>)
        }

        return (
            <div> 
                {game}
            </div>
        );
    }
}

const mapStateToProps = state =>{
    return{
        questions: state.Questions,
        qstNumber: state.numberOfQuestions
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onRestartGame: () => dispatch({type: actionTypes.RESTART_GAME})
    };  
};

export default connect(mapStateToProps,mapDispatchToProps)(GameStarted);