import React from 'react';
import { NavLink, Route, Redirect } from 'react-router-dom';
import { BrowserRouter } from 'react-router-dom';
import { connect } from 'react-redux';

import Game from '../Game/Game';
import '../../styles/App.css';
import Questioneditor from '../Questioneditor/Questioneditor';
import * as actionTypes from '../../store/actions';

class App extends React.Component {
  componentWillMount(){
    this.props.restartPage();
  }
  
  handleClick = (event) => {
    if(this.props.running){
      if(!window.confirm("Biztos el szeretnéd hagyni az oldalt? Ezzel félbeszakítod a játékot.")){
          event.preventDefault();
      }else{
        this.props.restartPage();
      } 
    }
  }

  render() {
    return (
      <BrowserRouter>  
            <div>
                <header className="menu">    
                    <nav>
                        <ul>
                            <li><NavLink to="/game">Játék</NavLink></li>
                            <li><NavLink onClick={this.handleClick} to="/questions">Kérdés kezelő</NavLink></li>
                        </ul>
                    </nav>
                </header>
                <Redirect from="/*" to="/game"/>
                <Route path="/game" exact component={Game}/>
                <Route path="/questions" exact component={Questioneditor}/>
            </div>
      </BrowserRouter>
    );
  }
}

const mapStateToProps = state =>{
  return{
      running: state.hasGameStarted
  }
}

const mapDispatchToProps = dispatch => {
  return {
      restartPage: () => dispatch({type: actionTypes.RESTART_GAME})
  };
};

export default connect(mapStateToProps,mapDispatchToProps)(App);

