import React from 'react';
import '../../styles/Question.css';
import '../../styles/Game.css';

const Question = (props) => {
    return (
        <div className="question">
            <p>Kérdés: {props.question.question}</p>
            <p>Első válasz: {props.question.choiceOne} &emsp;  Második válasz: {props.question.choiceTwo}</p>
            <p>Harmadik válasz: {props.question.choiceThree} &emsp;  Negyedik válasz: {props.question.choiceFour}</p>
            <p>Helyes válasz sorszáma: {props.question.answer}</p>
            <p><button className="styledButton"onClick={props.delete}>Törlés</button></p>
        </div>
    )
}

export default Question;