import * as actionTypes from './actions';

const initalState = {
    isThereQuestion: false,
    Questions: [],
    numberOfQuestions: 0,
    hasGameStarted: false,
}

const reducer = (state = initalState, action) =>{
    switch (action.type){
        case actionTypes.ADD_QUESTION:
            const question = {
                question: action.question,
                choiceOne: action.cone,
                choiceTwo: action.ctwo,
                choiceThree: action.cthree,
                choiceFour: action.cfour,
                answer: action.ans,
                id: Date.now()
            }
            return{ 
                ...state,
                numberOfQuestions: state.numberOfQuestions + 1,
                Questions: state.Questions.concat(question)
            }
        case actionTypes.DELETE_QUESTION:
            const updatedArray = state.Questions.filter(Questions => Questions.id !== action.id);
            return{
                ...state,
                numberOfQuestions: state.numberOfQuestions - 1,
                Questions: updatedArray
            }
        case actionTypes.START_GAME:
            return{
                ...state,
                hasGameStarted: true
            }
        case actionTypes.RESTART_GAME:
            return{
                ...state,
                hasGameStarted: false
            }  
        default:
            return state;
    }
};

export default reducer;